
import { TRequestBodyRefreshToken, TRequestDataRefreshToken } from "../../services/types/api/api";
import {configStandartRequest, requestServer} from "../api/api";

export function setCookie(
  name: string,
  value: string,
  props: { [key: string]: any } & { expires?: number | Date | string } = {}
) {
  props = props || {};
  let exp = props.expires;
  if (typeof exp == 'number' && exp) {
    const d = new Date();
    d.setTime(d.getTime() + exp * 1000);
    exp = props.expires = d;
  }
  if (exp && (exp as Date).toUTCString) {
    props.expires = (exp as Date).toUTCString();
  }
  value = encodeURIComponent(value);
  let updatedCookie = name + '=' + value;
  for (const propName in props) {
    updatedCookie += '; ' + propName;
    const propValue = props[propName];
    if (propValue !== true) {
      updatedCookie += '=' + propValue;
    }
  }
  document.cookie = updatedCookie;
}

export function deleteCookie(name: string) {
  setCookie(name, '', { expires: -1 });
}

export function getCookie(name:string) {
  const matches = document.cookie.match(
    new RegExp('(?:^|; )' + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + '=([^;]*)')
  );
  return matches ? decodeURIComponent(matches[1]) : undefined;
}
export function refreshToken():void {
  requestServer<TRequestDataRefreshToken>("/auth/token", configStandartRequest<TRequestBodyRefreshToken>({ token: window.localStorage.getItem("refreshTokenBurger")??"" }, "POST"))
    .then(data => {
      console.log("token ")
      setCookie("accessTokenBurger", data.accessToken, { expires: 1200 });
    })
    .catch(() => {
      console.log('  token');
    })
}
